import * as Yup from 'yup';
import React, { useCallback } from 'react';
import { useSnackbar } from 'notistack5';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { LoadingButton } from '@material-ui/lab';
import {
  Box,
  Card,
  Grid,
  Stack,
  TextField,
  Typography,
  FormHelperText,
  CardContent,
  Chip,
  Autocomplete,
} from '@material-ui/core';
// utils
import { fData } from '../../utils/formatNumber';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// utils
import { nameRegExp } from '../../utils/constants/regexs';
// components
import { UploadSingleFile } from '../upload';

// ----------------------------------------------------------------------

const NewUserSchema = Yup.object().shape({
  postImage: Yup.mixed().required('Post image is required'),
  username: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('username is required'),
  question: Yup.string(),
  description: Yup.string(),
});

export default function UserPostNewForm() {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      postImage: null,
      username: 'johndoe',
      hashtags: [],
      question: 'Question One',
      description: '',
    },
    validationSchema: NewUserSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar('Create success', { variant: 'success' });
        navigate(PATH_DASHBOARD.brand.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  console.log('*** ', values, errors);

  const handleCoverDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('postImage', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={4}>
            <Card>
              <Box>
                <CardContent>
                  <Typography
                    // variant="caption"
                    sx={{
                      display: 'block',
                      textAlign: 'center',
                      color: 'text.secondary',
                    }}
                  >
                    Upload Image/Video/Audio
                  </Typography>
                  <UploadSingleFile
                    accept="image/*, video/*, audio/*"
                    file={values.postImage}
                    maxSize={3145728}
                    onDrop={handleCoverDrop}
                    error={Boolean(touched.postImage && errors.postImage)}
                  />
                  <Typography
                    variant="caption"
                    sx={{
                      mt: 2,
                      mx: 'auto',
                      display: 'block',
                      textAlign: 'center',
                      color: 'text.secondary',
                    }}
                  >
                    Allowed *.jpeg, *.jpg, *.png, *.gif
                    <br /> max size of {fData(3145728)}
                  </Typography>
                  <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
                    {touched.postImage && errors.postImage}
                  </FormHelperText>
                </CardContent>
              </Box>
            </Card>
          </Grid>

          <Grid item xs={12} md={8}>
            <Card sx={{ p: 3 }}>
              <CardContent>
                <Stack spacing={3}>
                  <Grid container spacing={2}>
                    <Grid item xs={12} md={6}>
                      <TextField
                        select
                        fullWidth
                        label="Select Username"
                        placeholder="Username"
                        {...getFieldProps('username')}
                        SelectProps={{ native: true }}
                        error={Boolean(touched.username && errors.username)}
                        helperText={touched.username && errors.username}
                      >
                        {['johndoe', 'evedoe'].map((option, index) => (
                          <option key={option + index} value={option}>
                            {option}
                          </option>
                        ))}
                      </TextField>
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <Autocomplete
                        multiple
                        freeSolo
                        value={values.hashtags}
                        onChange={(event, newValue) => {
                          setFieldValue('hashtags', newValue);
                        }}
                        options={[]}
                        renderTags={(value, getTagProps) =>
                          value.map((option, index) => (
                            <Chip key={option} size="small" label={option} {...getTagProps({ index })} />
                          ))
                        }
                        renderInput={(params) => <TextField {...params} label="Hashtags" />}
                      />
                    </Grid>
                  </Grid>

                  <TextField
                    select
                    fullWidth
                    label="Select Question"
                    placeholder="Question"
                    defaultValue={undefined}
                    {...getFieldProps('question')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.question && errors.question)}
                    helperText={touched.question && errors.question}
                  >
                    {['Question One', 'Question Two'].map((option, index) => (
                      <option key={option + index} value={option}>
                        {option}
                      </option>
                    ))}
                  </TextField>

                  <TextField
                    fullWidth
                    multiline
                    rows={3}
                    label="Description"
                    {...getFieldProps('description')}
                    error={Boolean(touched.description && errors.description)}
                    helperText={touched.description && errors.description}
                  />

                  <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                    <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                      Save Post
                    </LoadingButton>
                  </Box>
                </Stack>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
